import { NgModule } from "@angular/core";
import { RouterModule, Routes } from "@angular/router";
import { SurveyTypeComponent } from "./components/survey-type/survey-type.component";
import { TestTypeComponent } from "./components/test-type/test-type.component";
import { QuestionCategoryComponent } from "./components/question-category/question-category.component";
import { CourseTypeComponent } from "./components/course-type/course-type.component";
import { CourseCategoryComponent } from "./components/course-category/course-category.component";
import { ExpenseComponent } from "./components/expense/expense.component";
import { ReasonsComponent } from "./components/reasons/reasons.component";
import { ViewsManager } from "app/shared/resources/views-manager";



const routes: Routes = [
	{
		path: "survey-type",
		component: SurveyTypeComponent,
		data: { view:ViewsManager.survey_type },
	},
	{
		path: "test-type",
		component: TestTypeComponent,
		data: { view: ViewsManager.test_type },
	},

	{
		path: "question-category",
		component: QuestionCategoryComponent,
		data: { view: ViewsManager.question_category },
	},

	{
		path: "course-type",
		component: CourseTypeComponent,
		data: { view: ViewsManager.course_type },
	},

	{
		path: "course-category",
		component: CourseCategoryComponent,
		data: { view: ViewsManager.course_category},
	},

	{
		path: "expense",
		component: ExpenseComponent,
		data: { view: ViewsManager.expense },
	},

	{
		path: "reasons",
		component: ReasonsComponent,
		data: { view: ViewsManager.reasons },
	},
];

@NgModule({
	imports: [RouterModule.forChild(routes)],
	exports: [RouterModule],
})
export class TrainingSetupRoutingModule {}
