import { Injectable } from "@angular/core";
import { IPageParams } from "app/shared/interfaces/params";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { AppClientService } from "app/shared/services/app-client.service";
import { ISurveyTypeModel, ISurveyTypeRequest } from "../models/survey-type";

@Injectable({
	providedIn: "root",
})
export class SurveyTypeService extends AppClientService {
  constructor() {
    super();
  }

  create(data: ISurveyTypeRequest) {
    return this.post<IResponse<ISurveyTypeModel>>("TrainingSetup/SurveyType/Create", data);
  }

  update(data: ISurveyTypeRequest) {
    return this.post<IResponse<ISurveyTypeModel>>("TrainingSetup/SurveyType/Edit", data);
  }

  getList(params: IPageParams) {
    return this.get<IResponseList<ISurveyTypeModel[]>>("TrainingSetup/SurveyType/GetList", {
      params: params,
    });
  }



  getAll() {
    return this.get<IResponse<ISurveyTypeModel[]>>(" TrainingSetup/SurveyType/GetAll")
  }


  Delete(id: number) {
    return this.delete<IResponse>("TrainingSetup/SurveyType/Delete", {
      params: { Id: id },
    });
  }
}
