import { Injectable } from "@angular/core";
import { IPageParams } from "app/shared/interfaces/params";
import { AppClientService } from "app/shared/services/app-client.service";
import { IQuestionCategoryModel, IQuestionCategoryRequest } from "../models/question-category";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";

@Injectable({
	providedIn: "root",
})
export class QuestionCategoryService extends AppClientService {
	constructor() {
		super();
	}

	create(data: IQuestionCategoryRequest) {
		return this.post<IResponse<IQuestionCategoryModel>>(
			"TrainingSetup/QuestionCategory/Create",
			data
		);
	}

	update(data: IQuestionCategoryRequest) {
		return this.post<IResponse<IQuestionCategoryModel>>(
			"TrainingSetup/QuestionCategory/Edit",
			data
		);
	}

	getList(params: IPageParams) {
		return this.get<IResponseList<IQuestionCategoryModel[]>>(
			"TrainingSetup/QuestionCategory/GetList",
			{
				params: params,
			}
		);
	}

	getِAll() {
		return this.get<IResponse<IQuestionCategoryModel[]>>("TrainingSetup/QuestionCategory/GetAll");
	}

	Delete(Id: number) {
		return this.delete<IResponse>("TrainingSetup/QuestionCategory/Delete", {
			params: { Id: Id },
		});
	}
}
