import { Injectable } from "@angular/core";
import { AppClientService } from "app/shared/services/app-client.service";
import { IReasonsModel, IReasonsRequest } from "../models/reasons";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { IPageParams } from "app/shared/interfaces/params";

@Injectable({
	providedIn: "root",
})
export class ReasonsService extends AppClientService {
	constructor() {
		super();
	}
	create(data: IReasonsRequest) {
		return this.post<IResponse<IReasonsModel>>("TrainingSetup/Reason/Create", data);
	}

	update(data: IReasonsRequest) {
		return this.post<IResponse<IReasonsModel>>("TrainingSetup/Reason/Edit", data);
	}

	getList(params: IPageParams) {
		return this.get<IResponseList<IReasonsModel[]>>("TrainingSetup/Reason/GetList", {
			params: params,
		});
	}

	getAll() {
		this.get<IResponse<IReasonsModel[]>>("TrainingSetup/Reason/GetAll");
	}

	Delete(id: number) {
		return this.delete<IResponse>("TrainingSetup/Reason/Delete", {
			params: { Id: id },
		});
	}
}
