import { Injectable } from "@angular/core";
import { IPageParams } from "app/shared/interfaces/params";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { ITestTypeModel } from "../models/test-type";
import { ISurveyTypeRequest } from "../models/survey-type";
import { AppClientService } from "app/shared/services/app-client.service";

@Injectable({
  providedIn: "root",
})
export class TestTypeService extends AppClientService {
  constructor() {
    super();
  }

  create(data: ISurveyTypeRequest) {
    return this.post<IResponse<ITestTypeModel>>(
      "TrainingSetup/TestType/Create",
      data
    );
  }

  update(data: ISurveyTypeRequest) {
    return this.post<IResponse<ITestTypeModel>>(
      "TrainingSetup/TestType/Edit",
      data
    );
  }

  getList(params: IPageParams) {
    return this.get<IResponseList<ITestTypeModel[]>>(
      "TrainingSetup/TestType/GetList",
      {
        params: params,
      }
    );
  }

  getAll() {
    return this.get<IResponse<ITestTypeModel[]>>(
      "TrainingSetup/TestType/GetAll"
    );
  }

  Delete(Id: number) {
    return this.delete<IResponse>("TrainingSetup/TestType/Delete", {
      params: { Id: Id },
    });
  }
}
