import { Injectable } from "@angular/core";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { AppClientService } from "app/shared/services/app-client.service";
import { IExpenseModel, IExpenseRequest } from "../models/expense";
import { IPageParams } from "app/shared/interfaces/params";

@Injectable({
	providedIn: "root",
})
export class ExpenseService extends AppClientService {
	constructor() {
		super();
	}

	create(data: IExpenseRequest) {
		return this.post<IResponse<IExpenseModel>>("TrainingSetup/Expense/Create", data);
	}

	update(data: IExpenseRequest) {
		return this.post<IResponse<IExpenseModel>>("TrainingSetup/Expense/Edit", data);
	}

	getList(params: IPageParams) {
		return this.get<IResponseList<IExpenseModel[]>>("TrainingSetup/Expense/GetList", {
			params: params,
		});
	}

	getAll() {
		this.get<IResponse<IExpenseModel[]>>("TrainingSetup/Expense/GetAll");
	}

	Delete(id: number) {
		return this.delete<IResponse>("TrainingSetup/Expense/Delete", {
			params: { Id: id },
		});
	}
}
