import { Component, OnInit } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import { MatTableDataSource } from "@angular/material/table";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IPageParams } from "app/shared/interfaces/params";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { CustomValidator } from "app/shared/validators/custom.validator";
import {
	IQuestionCategoryModel,
	IQuestionCategoryRequest,
} from "app/training-setup/models/question-category";
import { QuestionCategoryService } from "app/training-setup/services/question-category.service";

@Component({
	selector: "app-question-category",
	templateUrl: "./question-category.component.html",
	styleUrls: ["./question-category.component.scss"],
})
export class QuestionCategoryComponent extends AppHelpers implements OnInit {
	form: FormGroup;
	submitted: boolean = false;
	mandatoryColomns: string[] = ["Ser", "id", "en_Name", "ar_Name", "actions"];
	isLoaded: boolean = false;
	optionalColomns: string[] = [];
	dataSource: MatTableDataSource<IQuestionCategoryModel>;

	constructor(private _questionCategoryService: QuestionCategoryService) {
		super();
	}

	ngOnInit(): void {
		this.form = this.fb.group({
			id: [0, { nonNullable: true }],
			en_Name: [null, [Validators.required, CustomValidator.whiteSpace]],
			ar_Name: [null, [Validators.required, CustomValidator.whiteSpace]],
		});
	}

	get formCtrls() {
		return this.form.controls;
	}

	override onChangeBusinessAndCompanyId(): void {
		if (this.checkNoBusinessOrCompany()) this.getList(this.firstPage);
	}

	getList(page: number, pageSize?: number) {
		this.page.pageIndex = page;
		if (pageSize) this.page.pageSize = pageSize;
		let params: IPageParams = {
			PageNumber: page,
			pageSize: this.page.pageSize,
		};
		this._questionCategoryService
			.getList(params)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponseList<IQuestionCategoryModel[]>) => {
				if (res.message.messageType == MessageTypes.Success) {
					this.dataSource = new MatTableDataSource(res.data);
					this.isLoaded = true;
					this.page.length = res.meta.totalItemCount;
				} else this.errorList(res.message.messages);
			});
	}

	submit() {
		this.submitted = true;
		if (this.form.valid) {
			const model: IQuestionCategoryRequest = {
				id: this.formCtrls.id.value,
				ar_Name: this.formCtrls.ar_Name.value,
				en_Name: this.formCtrls.en_Name.value,
			};
			model.id == 0 ? this.create(model) : this.update(model);
		}
	}

	create(model: IQuestionCategoryRequest) {
		this.showLoading();
		this._questionCategoryService
			.create(model)
			.subscribe((res: IResponse<IQuestionCategoryModel>) => {
				if (res?.message?.messageType == MessageTypes.Success) {
					this.clearForm();
					this.getList(this.lastPage, this.page.pageSize);
					this.addSuccessfullyMsg(res?.message?.messageTypeName);
				} else this.errorList(res.message.messages);
				this.hideLoading();
			});
	}

	update(model: IQuestionCategoryRequest) {
		this.showLoading();
		this._questionCategoryService
			.update(model)
			.subscribe((res: IResponse<IQuestionCategoryModel>) => {
				if (res.message.messageType == MessageTypes.Success) {
					this.clearForm();
					this.getList(this.currentPage, this.page.pageSize);
					this.updateSuccessfullyMsg(res?.message?.messageTypeName);
				} else this.errorList(res.message.messages);
				this.hideLoading();
			});
	}

	questionCategoryToEdit(questionCategory: IQuestionCategoryModel) {
		this.form.patchValue(questionCategory);
	}

	onDelete(questionCategory: IQuestionCategoryModel): void {
		let msg = this.msgTranslate(questionCategory.ar_Name, questionCategory.en_Name);
		this.deleateDialog(msg).subscribe((confirm) => {
			if (confirm) this.delete(questionCategory);
		});
	}

	delete(questionCategory: IQuestionCategoryModel) {
		this.showLoading();
		this._questionCategoryService.Delete(questionCategory.id).subscribe((res: IResponse) => {
			if (res.message.messageType == MessageTypes.Success) {
				this.deleteSuccessfullyMsg(res?.message?.messageTypeName);
				if (this.dataSource.data.length == 1 && this.currentPage > 1) {
					this.getList(this.prevPage, this.page.pageSize);
				} else {
					this.getList(this.currentPage, this.page.pageSize);
				}
				if (questionCategory.id == this.formCtrls.id.value) this.clearForm();
			} else this.errorList(res.message.messages);
			this.hideLoading();
		});
	}

	clearForm() {
		this.form.reset({ id: 0 });
		this.submitted = false;
	}
}
